@use 'base/variables' as v;
@use 'base/mixins' as m;

.camisa {
    padding: 1rem;
}

@include m.tablet {
    .camisa {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 2rem;
    }
}

.formulario {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-columns: subgrid;
    gap: 1rem;
}

.formulario__campo {
    border: 1rem solid v.$primarioOscuro;
    background-color: transparent;
    color: v.$blanco;
    font-size: 1.5rem;
    font-family: Arial, Helvetica, sans-serif;
    padding: 1rem;
    text-overflow: ellipsis;
    width: 100%;
}

.formulario__campo--opcion {
    color: v.$oscuro;
}

::placeholder {
    color: v.$blanco;
}

.formulario__submit {
    background-color: v.$secundario;
    border: none;
    font-size: 2rem;
    font-family: v.$fuente_principal;
    padding: 2rem;
    transition: all 0.3s ease;
    grid-column: 1 / 3;
}

.formulario__submit:hover {
    cursor: pointer;
    background-color: v.$secundarioOscuro;
}