html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: #9c27b0;
  font-size: 1.6rem;
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

p {
  font-size: 1.8rem;
  font-family: Arial, Helvetica, sans-serif;
  color: #ffffff;
}

.contenedor {
  max-width: 120rem;
  margin: 0 auto;
  padding-bottom: 2rem;
}

a {
  text-decoration: none;
}

.boton {
  background-color: #9c27b0;
  color: #212121;
  padding: 1rem 3rem;
  margin-top: 1rem;
  font-size: 2rem;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 0.5rem;
  text-align: center;
  border: none;
}

h1,
h2,
h3 {
  text-align: center;
  color: #ffce00;
  font-family: "Staatliches", cursive;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

img {
  width: 100%;
}

.header {
  display: flex;
  justify-content: center;
}
.header a {
  height: 157px;
}

.header__logo {
  margin: 3rem 0;
}

.navegacion {
  background-color: #891190;
  padding: 1rem 0;
  display: flex;
  justify-content: center;
}

.navegacion__enlace {
  font-family: "Staatliches", cursive;
  color: #ffffff;
  font-size: 3rem;
  margin-right: 2rem;
}

.navegacion__enlace:last-of-type {
  margin-right: 0;
}

.navegacion__enlace--activo,
.navegacion__enlace:hover {
  color: #ffce00;
}

.footer {
  background-color: #891190;
  padding: 1rem 0;
  margin-top: auto;
}

.footer__texto {
  font-family: "Staatliches", cursive;
  text-align: center;
  font-size: 2.2rem;
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}

@media (min-width: 768px) {
  .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
.producto {
  background-color: #891190;
  padding: 1rem;
}
.producto img:hover {
  -webkit-box-shadow: 0px 0px 5px 5px #ffffff;
  -moz-box-shadow: 0px 0px 5px 5px #ffffff;
  box-shadow: 0px 0px 10px 5px #ffffff;
}

.producto__nombre {
  font-size: 4rem;
}

.producto__precio {
  font-size: 2.8rem;
  color: #ffce00;
}

.producto__nombre,
.producto__precio {
  font-family: "Staatliches", cursive;
  margin: 1rem 0;
  text-align: center;
  line-height: 1.2;
}

.camisa {
  padding: 1rem;
}

@media (min-width: 768px) {
  .camisa {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }
}
.formulario {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-columns: subgrid;
  gap: 1rem;
}

.formulario__campo {
  border: 1rem solid #891190;
  background-color: transparent;
  color: #ffffff;
  font-size: 1.5rem;
  font-family: Arial, Helvetica, sans-serif;
  padding: 1rem;
  text-overflow: ellipsis;
  width: 100%;
}

.formulario__campo--opcion {
  color: #212121;
}

::placeholder {
  color: #ffffff;
}

.formulario__submit {
  background-color: #ffce00;
  border: none;
  font-size: 2rem;
  font-family: "Staatliches", cursive;
  padding: 2rem;
  transition: all 0.3s ease;
  grid-column: 1/3;
}

.formulario__submit:hover {
  cursor: pointer;
  background-color: #e9bb02;
}

.grafico {
  min-height: 30rem;
  background-repeat: no-repeat;
  background-size: cover;
  grid-column: 1/3;
}

.grafico--camisas {
  grid-row: 2/3;
  background-image: url(../img/grafico1.jpg);
}

.grafico--node {
  background-image: url(../img/grafico2.jpg);
  grid-row: 8/9;
}

@media (min-width: 768px) {
  .grafico--node {
    grid-row: 5/6;
    grid-column: 2/4;
    background-image: url(../img/grafico2.jpg);
  }
}
.nosotros {
  display: grid;
  grid-template-rows: repeat(2, auto);
  gap: 2rem;
  padding: 1rem;
}

.nosotros p {
  text-align: justify;
  margin-top: 0;
}

@media (min-width: 768px) {
  .nosotros {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
  }

  .nosotros__imagen {
    grid-column: 2/3;
  }
}
.nosotros__imagen {
  grid-row: 1/2;
}

.bloques {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-rows: repeat(2, auto);
  gap: 1rem;
  padding: 1rem;
}

.bloque {
  text-align: center;
}

@media (min-width: 768px) {
  .bloques {
    grid-template-columns: repeat(4, auto);
  }
}
.bloque__titulo {
  margin: 0px;
}

.bloque__descripcion {
  text-align: center;
}