@use 'base/variables' as v;
@use 'base/mixins' as m;

.nosotros {
    display: grid;
    grid-template-rows: repeat(2,auto);
    gap: 2rem;
    padding: 1rem;
}

.nosotros p {
    text-align: justify;
    margin-top: 0;
}

@media (min-width: 768px) {
    .nosotros {
        grid-template-columns: repeat(2,1fr);
        column-gap: 2rem;
    }

    .nosotros__imagen {
        grid-column: 2 / 3;
    }
}

.nosotros__imagen {
    grid-row: 1 / 2;
}

.bloques {
    display: grid;
    grid-template-columns: repeat(2,auto);
    grid-template-rows: repeat(2,auto);
    gap: 1rem;
    padding: 1rem;
}

.bloque {
    text-align: center;
}

@include m.tablet {
    .bloques {
        grid-template-columns: repeat(4,auto);
    }
}

.bloque__titulo {
    margin: 0px;
}

.bloque__descripcion {
    text-align: center;
}