@use "base/variables" as v;
@use "base/mixins" as m;

.producto {
  background-color: v.$primarioOscuro;
  padding: 1rem;
  img:hover {
    -webkit-box-shadow: 0px 0px 5px 5px v.$blanco;
    -moz-box-shadow: 0px 0px 5px 5px v.$blanco;
    box-shadow: 0px 0px 10px 5px v.$blanco;
  }
}

.producto__nombre {
  font-size: 4rem;
}

.producto__precio {
  font-size: 2.8rem;
  color: v.$secundario;
}

.producto__nombre,
.producto__precio {
  font-family: v.$fuente_principal;
  margin: 1rem 0;
  text-align: center;
  line-height: 1.2;
}
